// jest.setup.js

import { NativeModules } from 'react-native';

/* ----------------------- ⚙️ Mocks système React Native ----------------------- */
jest.mock('react-native/Libraries/Settings/Settings', () => ({
  get: jest.fn(() => 'light'),
  addEventListener: jest.fn(),
  removeEventListener: jest.fn(),
}));

NativeModules.SettingsManager = NativeModules.SettingsManager || {
  settings: { AppleLocale: 'en_US' },
};

/* ----------------------- 📦 Mocks des dépendances tierces ----------------------- */
jest.mock('@react-native-async-storage/async-storage', () =>
  require('@react-native-async-storage/async-storage/jest/async-storage-mock')
);

jest.mock('expo-font', () => ({
  useFonts: () => [true],
  isLoaded: () => true,
}));

jest.mock('expo-haptics', () => ({
  impactAsync: jest.fn(),
}));

jest.mock('expo-modules-core', () => ({
  NativeModulesProxy: {},
  EventEmitter: jest.fn(),
  requireNativeViewManager: jest.fn(() => ({})),
}));

jest.mock('expo-linear-gradient', () => {
  const React = require('react');
  const { View } = require('react-native');
  return {
    LinearGradient: ({ children, colors = ['transparent'], style, ...props }) => (
      <View style={[style, { backgroundColor: colors[0] }]} {...props}>
        {children}
      </View>
    ),
  };
});

/* ------------------------ 🔄 Mocks d’animations React Native ------------------------ */
jest.mock('react-native', () => {
  const RN = jest.requireActual('react-native');

  const mockAnimatedValue = {
    setValue: jest.fn(),
    addListener: jest.fn(() => 'mockListenerId'),
    removeListener: jest.fn(),
    removeAllListeners: jest.fn(),
    interpolate: jest.fn(() => mockAnimatedValue),
    stopAnimation: jest.fn(),
    resetAnimation: jest.fn(),
    __getValue: jest.fn(() => 0),
    _value: 0,
    _offset: 0,
  };

  const createMockAnimation = () => ({
    start: jest.fn(cb => cb && cb({ finished: true })),
    stop: jest.fn(),
    reset: jest.fn(),
  });

  return {
    ...RN,
    Animated: {
      ...RN.Animated,
      Value: jest.fn(() => mockAnimatedValue),
      ValueXY: jest.fn(() => ({
        x: mockAnimatedValue,
        y: mockAnimatedValue,
        setValue: jest.fn(),
        setOffset: jest.fn(),
        flattenOffset: jest.fn(),
        extractOffset: jest.fn(),
        addListener: jest.fn(() => 'mockListenerId'),
        removeListener: jest.fn(),
        stopAnimation: jest.fn(),
        resetAnimation: jest.fn(),
        getLayout: jest.fn(() => ({ left: mockAnimatedValue, top: mockAnimatedValue })),
        getTranslateTransform: jest.fn(() => [
          { translateX: mockAnimatedValue },
          { translateY: mockAnimatedValue },
        ]),
      })),
      timing: jest.fn(createMockAnimation),
      spring: jest.fn(createMockAnimation),
      decay: jest.fn(createMockAnimation),
      sequence: jest.fn(() => createMockAnimation()),
      parallel: jest.fn(() => createMockAnimation()),
      stagger: jest.fn(() => createMockAnimation()),
      delay: jest.fn(() => createMockAnimation()),
      loop: jest.fn(() => createMockAnimation()),
      add: jest.fn(() => mockAnimatedValue),
      subtract: jest.fn(() => mockAnimatedValue),
      multiply: jest.fn(() => mockAnimatedValue),
      divide: jest.fn(() => mockAnimatedValue),
      modulo: jest.fn(() => mockAnimatedValue),
      diffClamp: jest.fn(() => mockAnimatedValue),
      event: jest.fn(() => jest.fn()),
      forkEvent: jest.fn(() => jest.fn()),
      unforkEvent: jest.fn(),

      View: RN.Animated.View,
      Text: RN.Animated.Text,
      ScrollView: RN.Animated.ScrollView,
      Image: RN.Animated.Image,
      Easing: {
        linear: t => t,
        ease: t => t,
        quad: t => t * t,
        cubic: t => t * t * t,
        poly: n => t => Math.pow(t, n),
        sin: t => 1 - Math.cos(t * Math.PI / 2),
        circle: t => 1 - Math.sqrt(1 - t * t),
        exp: t => Math.pow(2, 10 * (t - 1)),
        elastic: () => t => t,
        back: () => t => t,
        bounce: t => t,
        bezier: () => t => t,
        in: easing => easing,
        out: easing => t => 1 - easing(1 - t),
        inOut: easing => t =>
          t < 0.5 ? easing(t * 2) / 2 : (2 - easing((1 - t) * 2)) / 2,
      },
    },
  };
});

/* ----------------------- 🩹 Polyfills globaux (setImmediate) ----------------------- */
if (typeof global.setImmediate === 'undefined') {
  global.setImmediate = (fn, ...args) => setTimeout(fn, 0, ...args);
}
if (typeof global.clearImmediate === 'undefined') {
  global.clearImmediate = id => clearTimeout(id);
}

/* ----------------------- 🚫 Suppression de warnings inutiles ----------------------- */
const originalConsole = { ...console };

global.console = {
  ...console,
  error: jest.fn(message => {
    if (
      typeof message === 'string' &&
      message.includes('Warning: An update to') &&
      message.includes('was not wrapped in act')
    ) {
      return;
    }
    originalConsole.error(message);
  }),
  warn: jest.fn(message => {
    if (
      typeof message === 'string' &&
      message.includes('Warning: An update to') &&
      message.includes('was not wrapped in act')
    ) {
      return;
    }
    originalConsole.warn(message);
  }),
};
